function init() {
	let timerEl = document.querySelector('.js-timer-countdown');
	let minutesEl = timerEl.querySelector('.js-timer-countdown__minutes');
	let secondsEl = timerEl.querySelector('.js-timer-countdown__seconds');

	function renderSeconds(minutesVal, secondsVal) {
		minutesVal.toString().length === 1 ? minutesVal = '0' + minutesVal : minutesVal;
		secondsVal.toString().length === 1 ? secondsVal = '0' + secondsVal : secondsVal;

		minutesEl.innerHTML = minutesVal;
		secondsEl.innerHTML = secondsVal;
	}

	let startCount = {
		minutes: 2,
		seconds: 0,
	}
	let finaleTime = startCount.minutes * 60 + startCount.seconds;

	function timerStep() {
		let minutesVal = parseInt(finaleTime / 60);
		let secondsVal = parseInt(finaleTime - 60 * minutesVal);

		renderSeconds(minutesVal, secondsVal);

		if (minutesVal === 0 && secondsVal <= 30) {
			timerEl.classList.add('js-discount-timer--is-ending');
		}

		if (finaleTime == 0) {
			// document.querySelector('.rate-plan__cells').classList.add('rate-plan__cells--hidden-discount');

			// popup.popupOpen(document.querySelector('#popup'));

			let tl = gsap.timeline({});

			tl.to('.rate-plan__cell-percentage',
				{
					opacity: 0,
				})
				.to('.rate-plan__cell-discount', {
					opacity: 0,
				}, '<')
				.to('.rate-plan__cell-price', {
					opacity: 0,
				}, '<')
				.to('.rate-plan__cells', {
					onComplete: () => {
						document.querySelector('.rate-plan__cells').classList.add('rate-plan__cells--hidden-discount');
					},
				})
				.to('.rate-plan__cell-price', {
					opacity: 1,
				})
				.to('.rate-plan__cells', {
					delay: 0.15,
					onComplete: () => {
						popup.popupOpen(document.querySelector('#popup'));
					},
				});

			return 0;
		}

		finaleTime--;
		setTimeout(timerStep, 1000);
	}

	timerStep();
}

export default {
	init,
};
