import rc from './radio-custom';

const popupLinks = document.querySelectorAll('.popup-link');
const body = document.querySelector('body');
const html = document.documentElement;
const lockPadding = document.querySelectorAll('.lock-padding');
const timeout = 800;

let unlock = true;

function popupOpen(currentPopup) {
	if (currentPopup && unlock) {
		const popupActive = document.querySelector('.popup__mask--open');
		if (popupActive) {
			popupClose(popupActive, false);
		} else {
			bodyLock();
		}
		currentPopup.classList.add('popup__mask--open');
		currentPopup.addEventListener('click', function (e) {
			if (!e.target.closest('.popup__body')) {
				popupClose(e.target.closest('.popup__mask'));
			}
		});
	}
}

function popupClose(popupActive, doUnlock = true) {
	if (unlock) {
		popupActive.classList.remove('popup__mask--open');
		if (doUnlock) {
			bodyUnLock();
		}
	}
}

function bodyLock() {
	const lockPaddingValue = window.innerWidth - document.querySelector('.site').offsetWidth + 'px';

	if (lockPadding > 0) {
		for (let i = 0; i < lockPadding.length; i++) {
			const el = lockPadding[i];
			el.style.paddingRight = lockPaddingValue;
		}
	}
	body.style.paddingRight = lockPaddingValue;
	body.classList.add('lock');
	html.style.overflow = 'hidden';

	unlock = false;
	setTimeout(function() {
		unlock = true;
	}, timeout);
}

function bodyUnLock() {
	setTimeout(function(e) {
		for (let i = 0; i < lockPadding.length; i++) {
			const el = lockPadding[i];
			el.style.paddingRight = '0px';
		}
		body.style.paddingRight = '0px';
		body.classList.remove('lock');
		html.style.overflow = 'auto';
	}, timeout);

	unlock = false;
	setTimeout(function() {
		unlock = true;
	}, timeout);
}

function init() {
	rc.init();

	if (popupLinks.length > 0) {
		for (let i = 0; i < popupLinks.length; i++ ) {
			const popupLink = popupLinks[i];
			popupLink.addEventListener("click", function (e) {
				const popupName = popupLink.getAttribute('href').replace('#','');
				const currentPopup = document.getElementById(popupName);
				console.log(popupName);
				popupOpen(currentPopup);

				e.preventDefault;
			});
		}
	}

	const popupCloseBtn = document.querySelectorAll('.popup__close');
	if (popupCloseBtn.length > 0) {
		for (let i = 0; i < popupCloseBtn.length; i++) {
			const el = popupCloseBtn[i];
			el.addEventListener('click', function(e) {
				popupClose(el.closest('.popup__mask'));
				e.preventDefault;
			});
		}
	}

	document.addEventListener('keydown', function(e) {
		if (e.which === 27) {
			const popupActive = document.querySelector('.popup__mask--open');
			popupClose(popupActive);
		}
	});

	(function() {
		if (!Element.prototype.closest) {
			Element.prototype.closest = function(css) {
				var node = this;
				while (node) {
					if (node.matches(css)) return node;
					else node = node.parentElement;
				}
				return null;
			};
		}
	})();

	(function() {
		if (!Element.prototype.matches) {
			Element.prototype.matches = Element.prototype.matchesSelector ||
			Element.prototype.webkitMatchesSelector ||
			Element.prototype.mozMatchesSelector ||
			Element.prototype.mozMatchesSelector;
		}
	})();
}

export default {
	init,
	popupOpen,
};
