function insertAfter(referenceNode, newNode) {
	referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
}

function createCellItem(parent, data, dataItem) {
	// console.log(data);

	// Create item container
	const itemContainer = document.createElement('div');
	itemContainer.classList.add('rate-plan__cell-item');
	parent.appendChild(itemContainer);

	// Create Percentage container
	const percentageContainer = document.createElement('div');
	percentageContainer.classList.add('rate-plan__cell-percentage');

	// Complete percentageContainer
	const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
	const use = document.createElementNS('http://www.w3.org/2000/svg', 'use');
	use.setAttributeNS('http://www.w3.org/1999/xlink', 'xlink:href', './images/sprites.svg#percentage-star');
	svg.appendChild(use);
	percentageContainer.prepend(svg);

	const percentValue = document.createElement('div');
	percentValue.classList.add('rate-plan__cell-percentage-text');
	percentValue.textContent = `-${dataItem.percent * 100}%`;
	percentageContainer.appendChild(percentValue);

	// Add Content container
	const contentContainer = document.createElement('div');
	contentContainer.classList.add('rate-plan__cell-content');

	const numsContainer = document.createElement('div');
	numsContainer.classList.add('rate-plan__cell-nums');

	// period (limit)
	const itemLimit = document.createElement('div');
	itemLimit.classList.add('rate-plan__cell-period');
	itemLimit.textContent = data.name;

	// description for mobile
	const itemDescriptionMobile = document.createElement('div');
	itemDescriptionMobile.classList.add('rate-plan__cell-description', 'for-mobile');
	itemDescriptionMobile.innerHTML = dataItem.description;

	const itemCostContainer = document.createElement('div');
	itemCostContainer.classList.add('rate-plan__cell-cost');

	const itemPrice = document.createElement('div');
	itemPrice.classList.add('rate-plan__cell-price');

	// Цену можно высчитать исходя из полученной цены со скидкой (как сделано ниже).
	// itemPrice.textContent = Math.round(data.price / (1 - dataItem.percent)) + '₽';

	/**
	 * Однако тогда полученное число не будет таким округлённым как в дизайне!
	 * Чтобы соблюсти идентичность макету и не учитывать точное математическое значение
	 * лучше передавать вместе со скидочной ценой, ещё процент скидки и "красивую",
	 * с точки зрения маркетологов, реальную стоимость (price).
	 * Так же желательно передавать описание.
	 *
	 * В данном примере "красивая" цена, процент скидки и описание будут вынесены в
	 * отдельный объект dataRates
	 */
	itemPrice.textContent = dataItem.priceByDesign + '₽';

	const itemDiscount = document.createElement('div');
	itemDiscount.classList.add('rate-plan__cell-discount');
	itemDiscount.textContent = data.price + '₽';

	itemCostContainer.append(itemPrice, itemDiscount);

	// Append period (limit) & description (mobile) in nums container
	numsContainer.append(itemLimit, itemDescriptionMobile, itemCostContainer);

	// description for desktop
	const itemDescriptionDesktop = document.createElement('div');
	itemDescriptionDesktop.classList.add('rate-plan__cell-description', 'for-desktop');
	itemDescriptionDesktop.innerHTML = dataItem.description;

	// Append nums & description (desktop) in content container
	contentContainer.append(numsContainer, itemDescriptionDesktop);

	// Append persent mark & content container in Containers
	itemContainer.append(percentageContainer, contentContainer);
}

function createPopupOfferItem(parent, data, dataItem, index) {
	// Create Elements

	// Create Label (root for item)
	const itemContainer = document.createElement('label');
	itemContainer.classList.add('radio-button', 'popup__offer-list-item');

	// Create offer Top part
	const partOfferTop = document.createElement('div');
	partOfferTop.classList.add('popup__offer-top');

	// Create container for Top part contents
	const partOfferTopContent = document.createElement('div');
	partOfferTopContent.classList.add('popup__offer-top-content');
	// Create Limit
	const offerLimit = document.createElement('div');
	offerLimit.classList.add('popup__offer-limit');
	offerLimit.textContent = data.name;
	// Create Price for Desktop
	const offerPriceDesktop = document.createElement('div');
	offerPriceDesktop.classList.add('popup__offer-price', 'text--cross', 'for-desktop');
	offerPriceDesktop.textContent = dataItem.priceByDesign + '₽';

	// Create container for Input
	const partOfferTopRightContent = document.createElement('div');
	partOfferTopRightContent.classList.add('popup__offer-top-right-content');
	// Create Input container
	const inputContainer = document.createElement('div');
	inputContainer.classList.add('radio-button__input');
	// Create Input
	const input = document.createElement('input');
	input.type = 'radio';
	input.name = 'offer';
	input.value = index;
	// Create Input Custom
	const inputCustom = document.createElement('div');
	inputCustom.classList.add('radio-button__input-custom');

	// Create offer Separator part
	const partSeparator = document.createElement('div');
	partSeparator.classList.add('popup__offer-separator', 'for-desktop');

	// Create offer Bottom part
	const partOfferBottom = document.createElement('div');
	partOfferBottom.classList.add('popup__offer-bottom');

	// Create offer Discount
	const offerDiscount = document.createElement('div');
	offerDiscount.classList.add('popup__offer-discount');
	offerDiscount.textContent = data.price + '₽';

	// Create offer Percent
	const offerPercent = document.createElement('div');
	offerPercent.classList.add('popup__offer-percent');
	// Create percent image
	const percentSvg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
	const percentUse = document.createElementNS('http://www.w3.org/2000/svg', 'use');
	percentUse.setAttributeNS('http://www.w3.org/1999/xlink', 'xlink:href', './images/sprites.svg#percentage-star');
	percentSvg.appendChild(percentUse);
	// Create percent Value
	const percentValue = document.createElement('div');
	percentValue.classList.add('popup__offer-percent-text');
	percentValue.textContent = `-${dataItem.percent * 100}%`;

	// Create offer Price for Mobile
	const offerPriceMobile = document.createElement('div');
	offerPriceMobile.classList.add('popup__offer-price', 'text--cross', 'for-mobile');
	offerPriceMobile.textContent = dataItem.priceByDesign + '₽';

	// Appent root container
	parent.appendChild(itemContainer);
	// Append parts Top, Separator & Bottom
	itemContainer.append(partOfferTop, partSeparator, partOfferBottom);
	// Append container for content to Top part
	partOfferTop.append(partOfferTopContent, partOfferTopRightContent);
	// Append Limit & Price to Top Content
	partOfferTopContent.append(offerLimit, offerPriceDesktop);
	// Append Input Container
	partOfferTopRightContent.append(inputContainer);
	// Append Input & Custom input
	inputContainer.append(input, inputCustom);
	// Append Discount & Price for Mobile to part Bottom
	partOfferBottom.append(offerDiscount, offerPriceMobile);
	// Append offer Percent to Discount
	offerDiscount.append(offerPercent);
	// Append Percent Image SVG & percent Value
	offerPercent.append(percentSvg, percentValue);
}

function genRateCells(dataRate) {
	let cellIndex = 0;
	let popupIndex = 0;

	// Create Cell Container
	const parent = document.querySelector('.rates .col--2');
	const cellContainer = document.createElement('div');
	cellContainer.classList.add('rate-plan__cells');
	parent.prepend(cellContainer);

	// Create Popup Offers Container
	const popupRoot = document.querySelector('.js-popup-offers');
	const popupOfferContainer = document.createElement('div');
	const offersAfterThisPlace = popupRoot.querySelector('.js-space-above-offers');
	popupOfferContainer.classList.add('popup__offer-list');
	insertAfter(offersAfterThisPlace, popupOfferContainer);

	// Create cell items
	dataRate.forEach(function(item, index) {

		// To Home page Cell
		if (item.isPopular === true) {
			if (cellIndex >= 0 && cellIndex <= 3) {
				createCellItem(cellContainer, dataRate[index], dataRates.page[cellIndex]);
				cellIndex++;
			} else {
				createCellItem(cellContainer, dataRate[index], '');
			}
		}

		// To Popup
		else if (item.isDiscount === true) {
			if (popupIndex >= 0 && popupIndex <= 2) {
				createPopupOfferItem(popupOfferContainer, dataRate[index], dataRates.popup[popupIndex], popupIndex);
				popupIndex++;
			} else {
				createPopupOfferItem(popupOfferContainer, dataRate[index], '', popupIndex);
			}
		}
	});
}

function getRateData() {
	fetch('https://t-pay.iqfit.app/subscribe/list-test')
		.then((response) => {
			return response.json();
		})
		.then((data) => {
			genRateCells(data);
		})
		.catch(error => {
			console.log(error);
		});
}

function init() {

	timerCountdown.init();
	popup.init();
	getRateData();

}

export default {
	init,
};
