function checkRadio(radios) {
	radios.forEach(function(item, index) {
		if (item.checked) {
			item.closest('label.radio-button').classList.add('radio-button--active');
		} else {
			item.closest('label.radio-button').classList.remove('radio-button--active');
		}
	});
}

function init() {
	let radios = document.querySelectorAll('input[type="radio"]');

	radios.forEach(function(item) {
		item.addEventListener('click', function() {
			checkRadio(radios);
		});
	});

	checkRadio(radios);
}

export default {
	init,
};
